<template>
  <div class="home">
    <transition name="fade">
      <div class="tips" v-if="tips.toggle">{{tips.desc}}</div>
    </transition>
    <div class="top">
      <img class="bg-img" src="../assets/cosleep_home_bg_forest@2x.png" />
      <div class="img-outer">
        <img class="img" v-if="toggle" src="../assets/Cosleep_Login-in_image_cat2@2x.png">
        <img class="img" v-else src="../assets/Cosleep_Login-in_image_cat1@2x.png">
      </div>
    </div>
    <div class="center">
      <input class="myinput" placeholder="请输入礼品码" v-model="code" />
      <div class="submit" @click="login">马上兑换</div>
    </div>
    <!-- <a :href="link_to">
      <div class="link">
        <i class="iconfont link-icon">&#xe644;</i>会员抽奖>>
      </div>
    </a> -->
    <transition name="fade">
      <div class="notice" v-if="template_show">
        <div class="template">
          <div class="close" @click="template_show = false">×</div>
          <div class="type-1" v-if="type === 1">
            <img class="img" src="../assets/cosleep_window_img_complete@3x.png">
            <p class="p-success">兑换成功</p>
            <p class="p-detail">兑换成功，可到【开通会员】中查看</p>
            <div class="submit p-submit" @click="toHuiYuanCenter">马上查看</div>
          </div>
          <div class="type-1" v-if="type === 2">
            <img class="img" src="../assets/cosleep_window_img_fail@2x.png">
            <p class="p-success">兑换失败</p>
            <p class="p-detail">{{err_msg}}</p>
            <div class="submit p-submit" @click="template_show = false">重新输入</div>
          </div>
          <div class="type-3" v-if="type === 3">
            <img class="img" src="../assets/cosleep_window_img_login@2x.png">
            <p class="p-success">登陆绑定</p>
            <div class="type3-input">
              <div class="input-1">
                <i class="iconfont">&#xe645;</i>
                <input v-model="phone" class="phone" placeholder="请输入手机号"/>
              </div>
              <div class="input-2">
                <i class="iconfont">&#xe63f;</i>
                <input v-model="assign" class="assign" placeholder="请输入验证码"/>
                <div class="send-assign" @click="sendAssign">{{assign_count}}</div>
              </div>
            </div>
            <div class="desc">·为了更好保护你的权益，请绑定手机号·</div>
            <div class="submit type-3-sumbit" @click="bindPhone">马上绑定</div>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import { callAppRouter } from '@/js/tool'
import Axios from 'axios'
import qs from 'qs'
export default {
  name: 'home',
  data () {
    return {
      tips: { toggle: false, desc: '' },
      home_height: 0,
      toggle: true,
      template_show: false,
      type: 999,
      code: '',
      url: 'https://api.debug.psy-1.com',
      link_to: 'https://web.debug.psy-1.com/cosleep/lottery',
      err_msg: '当前输入的兑换码无效或过期，请确认兑换码',
      openid: '',
      got_openid: false,
      phone: '',
      assign: '',
      assign_count: '获取',
      assign_count_interval: null,
      assign_count_busy: false,
      toggle_timeout: null,
      redirect_interval: null
    }
  },
  components: {
  },
  watch: {
    code () {
      this.toggle = false
      clearTimeout(this.toggle_timeout)
      this.toggle_timeout = setTimeout(() => {
        this.toggle = true
      }, 3000)
    }
  },
  methods: {
    toHuiYuanCenter () {
      callAppRouter('Redirect', {
        'code': 10017
      }, (res, ed) => {
      })
    },
    submit () {
      if (!this.code) {
        console.log('不能为空')
        this.err_msg = '当前输入的兑换码不能为空，请确认兑换码'
        this.template_show = true
        this.type = 2
        return
      }
      // eslint-disable-next-line
      $.post(this.$parent.url + '/web/v1/cosleepmp/vip/code/exchange', { openid: this.openid, code: this.code }, (res) => {
        this.err_msg = res.msg
        if (parseInt(res.status) === 28) {
          this.type = 3
        } else
        if (parseInt(res.status) === 1) {
          this.err_msg = '兑换成功，可到【购买记录】中查看'
          this.type = 1
        } else {
          this.type = 2
        }
        this.$nextTick(() => {
          this.template_show = true
        })
      })
    },
    Tips (message) {
      this.tips.desc = message
      if (!this.tips.busy) {
        this.tips.toggle = true
        this.tips.busy = true
        setTimeout(() => {
          this.tips.toggle = false
          this.tips.busy = false
        }, 2000)
      }
    },
    getOpenId () {
      callAppRouter('Login', {}, (res, ed) => {
        let mesg = null
        if (typeof ed === 'string') {
          mesg = JSON.parse(ed)
        } else {
          mesg = ed
        }
        this.openid = mesg.data.openid
        console.log(this.openid)
      })
    },
    login (e, init = false) {
      callAppRouter('isLogin', {}, (res, ed) => {
        console.log('??')
        let mesg = null
        if (typeof ed === 'string') {
          mesg = JSON.parse(ed)
        } else {
          mesg = ed
        }
        if (mesg.msg === '已登录') {
          console.log('已登陆')
          if (!init) {
            this.submit()
          }
          if (this.got_openid) {
            clearInterval(this.redirect_interval)
            return
          }
          this.getOpenId()
        } else {
          if (init) {
            console.log(init)
            console.log('??zzasdasdasinit')
            return
          }
          callAppRouter('Redirect', { code: '1024' }, (res, ed) => {
            this.redirect_interval = setInterval(() => {
              this.login(e, true)
            }, 500)
          })
        }
      })
    },
    bindPhone () {
      if (!this.assign) {
        this.Tips('请输入验证码')
        return 0
      }
      if (!this.phone) {
        this.Tips('请输入手机号')
        return 0
      }
      const dataObj = qs.stringify({
        openid: this.openid,
        mobile: this.phone,
        code: this.assign
      })
      Axios({ method: 'POST',
        url: this.$parent.url + '/web/v1/cosleep/user/mobile/bind',
        data: dataObj,
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
        }
      }).then((res) => {
        if (parseInt(res.data.status) === 34) {
          this.Tips('验证码输入错误')
        }
        if (parseInt(res.data.status) === 18) {
          this.Tips('该手机号码已被其他账户绑定')
        }
        if (parseInt(res.data.status) === 1) {
          this.Tips('绑定成功')
          this.template_show = false
        }
      })
    },
    getAssign () {
      // Axios.defaults.headers['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8'
      // const dataObj = qs.stringify({ 'mobile': '14748856650' })
      Axios({ method: 'POST',
        url: this.$parent.url + '/web/v1/cosleep/verifycode/send',
        data: { mobile: this.phone }
      }).then((res) => {
        console.log(res.data.msg === '成功')
      })
    },
    sendAssign () {
      if (this.assign_count_busy) {
        return
      }
      if (!this.phone) {
        this.Tips('请输入手机号')
        return 0
      }
      if (!/^[0-9]*[1-9][0-9]*$/g.test(parseInt(this.phone)) || this.phone.split('').length !== 11) {
        this.Tips('请输入正确的手机号')
        return
      }
      this.assign_count_busy = true
      this.getAssign()
      let count = 60
      this.assign_count = count + 's'
      this.assign_count_interval = setInterval(() => {
        count -= 1
        this.assign_count = count + 's'
        if (count === 0) {
          this.assign_count_busy = false
          setTimeout(() => {
            this.assign_count = '获取'
          }, 1000)
          clearInterval(this.assign_count_interval)
        }
      }, 1000)
    }
  },
  mounted () {
    console.log('mounted')
    callAppRouter('isLogin', {}, (res, ed) => {
      console.log('call isLogin')
      let mesg = null
      if (typeof ed === 'string') {
        mesg = JSON.parse(ed)
      } else {
        mesg = ed
      }
      if (mesg.msg === '已登录') {
        this.getOpenId()
      }
    })
  }
}
</script>

